import React, {useState} from 'react';
import { connect } from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {SaveOutlined} from "@ant-design/icons";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Card, Col, Form, Input,message, Row} from "antd";
import ApiService from "../../../../../../../../../services/api.service";
function mapStateToProps(state) {
    return {

    };
}

function Index()  {
    const [ loading, setLoading ] = useState(false)

    const [ form ] = Form.useForm();
    const onFinish = ()=> {
        setLoading(true)
        form.validateFields()
            .then((value)=> {

                new ApiService({
                    url:`/api/v1/announcement/categories`,
                    body: value
                })
                    .post()
                    .then((response)=> {
                        if(response?.success){
                            message.success(response?.message ?? "Successfully: created")
                        }else{
                            message.info(response?.message ?? "-")
                        }
                        setLoading(false)
                    })
                    .catch((err)=> {
                        message.error(err?.message ?? "-")
                        setLoading(false)
                    })

            })
            .catch((err)=> {
                setLoading(false)
            })
    }

    return (
        <>
            <HeroComponents
                loading={false}
                name={"Create Announcement Category"}
                positionName={['General','Announcement','Category']}
                thumbnail={null}
                positionColor={'cyan'}
                extra={<>
                    <ButtonPermission
                        permission={['admin']}
                        theme={{
                            type: "notification",
                            property: {
                                message: "Access Denied",
                                description: "Anda tidak dapat menyimpan ke database "
                            }
                        }}
                        config={{
                            icon: <SaveOutlined />,
                            type: "primary",
                            danger:false,
                            loading:loading ?? false,
                            ghost:true,
                            size: "small",
                            onClick: function () {
                                onFinish();
                            }
                        }}>
                        <span>Save</span>
                    </ButtonPermission>
                </>}
            />

            <div style={{marginTop:90}}>
                <ContainerTailwind>
                    <Form form={form} layout={'vertical'}>
                        <Row gutter={24}>
                            <Col xs={{span:24,order:1}} lg={{span:14,order:1}}>
                                <Card bordered={false}>
                                    <Form.Item hasFeedback name={'name'} label={'Name'} rules={[{required:true,message:"Can't be empty!"}]}>
                                        <Input/>
                                    </Form.Item>
                                    <Form.Item
                                        hasFeedback name={'description'} label={'Description'} rules={[{required:false,message: "Can't be empty!"}]}>
                                        <Input.TextArea
                                            allowClear
                                            showCount={true}
                                            autoSize={{ minRows: 4, maxRows: 6 }}
                                            size={"small"}
                                        />
                                    </Form.Item>
                                    {/*<Form.Item hasFeedback name={'slug'} label={'Slug'} rules={[{required:true,message:"Can't be empty!"}]}>*/}
                                    {/*    <Input/>*/}
                                    {/*</Form.Item>*/}
                                </Card>
                            </Col>
                        </Row>
                    </Form>
                </ContainerTailwind>
            </div>
        </>
    );
}

export default connect(
    mapStateToProps,
)(Index);